.card-container {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
}

.img-container {
  margin: 0 auto;
  width: 288px;
  height: 420px;
  overflow: hidden;
  border-radius: 25px;
}

.img-flip {
  position: relative;
  transition: all 0.6s ease;
  transform-style: preserve-3d;
}

.img-flip:hover {
  transform: rotateY(180deg);
}

.img-front {
  clip: rect(4px, 285px, 415px, 6px);
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
}

.img-back {
  clip: rect(4px, 568px, 416px, 289px);
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  left: 100%;
  transform: rotateY(180deg);
}
